import React from 'react'

const Reporte22=({contenido}) => {
    return(
        <div id="ID_reporte" >
                <div id="ID_imagen_reporte">
                    <center><h1>GRAFICA PARA LA TASA DE MORTALIDAD EN UN PAIS</h1></center>                  
                    <center><img src={contenido.img}/></center>
                </div>
                <div id="ID_reporte_contenido">
                    <center>
                    <h2>Tasa de mortalidad por coronavirus (COVID-19) en un pas.</h2>
                    <p>
                    El estudio surgi de la avalancha de informacin relacionada con la COVID, la enfermedad causada por el coronavirus SARS-2, que apuntaba a que la edad se asociaba a mayor mortalidad. Sin embargo, no haba evidencia slida para saber qu deba considerarse edad avanzada para esta enfermedad. Al mismo tiempo, se estaban comunicando gran cantidad de casos de fallecimientos en gente joven.
                    </p>
                    <br/>
                    <p>
                    Tasa de mortalidad por coronavirus (COVID-19) en un pas.:<br/>
                       {contenido.tasa}<br/>
                    </p>                        
                    <p>   
                       Autor: Wilfred Stewart Perez Solorzano<br/>Carnet:201408419
                    </p>
                    </center>
                </div>
            </div>
    )
}

export default Reporte22;