import React from 'react'

const Reporte1=({contenido}) => {

    return(
        <div id="ID_reporte" >
                <div id="ID_imagen_reporte">
                    <center><h1>GRAFICA DE TENDENCIA</h1></center>
                    
                    <center><img src={contenido.img}/></center>
                </div>
                <div id="ID_reporte_contenido">
                    <center>
                    <h2>Tendencia de la infeccin por Covid-19 en un Pas</h2>
                    <p>
                    La subregin del Caribe y el Ocano Atlntico sigue viendo una aceleracin de los casos de COVID-19, y algunos pases han declarado una quinta oleada de la pandemia en los ltimos das. Entre los 36 pases y territorios de la subregin, al menos la mitad de ellos han experimentado un aumento del 100% o ms de casos durante los ltimos 7 das en comparacin con los 7 das anteriores (rango: 100% - 879%).
                    </p>
                    <br/>
                    <p>
                       Ecuacion del modelo de regresion polinomial de grado 2:<br/>
                       {contenido.ecuacion}<br/>
                       Coeficiente de determinacion(R^2):<br/>
                       {contenido.val_r_cuadrado}<br/>
                       Conclusion:<br/>
                       {contenido.pendiente}
                       <br/>
                       <br/>
                       Autor: Wilfred Stewart Perez Solorzano<br/>Carnet:201408419
                    </p>
                    </center>
                </div>
            </div>
    )
}

export default Reporte1;